.sendForm {
    display: flex;
    justify-content: flex-end;
    justify-content: space-between;
    margin-top: 50px;
    

}



.textarea {
        margin-left: 70px;
        width: 976px;
        height: 40px;
        
        
        background: #F5F7FB;
        box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
        border-radius: 20px;
        
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        
}


.button {
    width: 120px;
    height: 40px;
    margin-right: 70px;


    background: #0066CC;
    border-radius: 20px;
    border: 1px solid #0066CC;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
}



